import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue';
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			editType: 'edit',
			id: '',
			name: '',
			tel: '',
			detailed: '',
			isDefault: false,
			cityPickerValue: [0, 0, 1],
			themeColor: '#007AFF',
			region: {
				label: "请点击选择地址",
				value: [],
				cityCode: ""
			}
		};
	},
	methods: {
		onCancel(e) {
			console.log(e)
		},
		chooseCity() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.region = e;
			this.cityPickerValue = e.value;
		},
		isDefaultChange(e) {
			this.isDefault = e.detail.value;
		},
		del() {
			uni.showModal({
				title: '删除提示',
				content: '你将删除这个收货地址',
				success: (res) => {
					if (res.confirm) {
						this.$api.DeleteaddressUser({
							addressId: this.id
						}, res => {
							if (res.code) {
								uni.navigateBack();
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});

		},
		save() {
			let data = {
				"addressName": this.name,
				"addresstel": this.tel,
				"region": this.region,
				"detailed": this.detailed,
				"isDefault": this.isDefault
			}
			if (this.editType == 'edit') {
				data.addressId = this.id
			}
			if (!data.addressName) {
				uni.showToast({
					title: '请输入收件人姓名',
					icon: 'none'
				});
				return;
			}
			if (!data.addresstel) {
				uni.showToast({
					title: '请输入收件人电话号码',
					icon: 'none'
				});
				return;
			}
			if (!data.detailed) {
				uni.showToast({
					title: '请输入收件人详细地址',
					icon: 'none'
				});
				return;
			}
			if (data.region.value.length == 0) {
				uni.showToast({
					title: '请选择收件地址',
					icon: 'none'
				});
				return;
			}
			uni.showLoading({
				title: '正在提交'
			})
			if (this.editType == 'edit') {
				this.$api.UpadtaaddressUser(data, res => {
					if (res.code) {
						uni.hideLoading();
						uni.navigateBack();
					}
				})
			} else {
				this.$api.AddaddressUser(data, res => {
					if (res.code) {
						uni.hideLoading();
						uni.navigateBack();
					}
				})
			}
		}
	},
	onLoad(e) {
		//获取传递过来的参数
		this.editType = e.type;
		if (e.type == 'edit') {
			this.id = e.addressId;
			this.$api.getAddressById({
				addressId: this.id
			}, res => {
				if (res.code) {
					this.name = res.data.addressName;
					this.tel = res.data.addresstel;
					this.isDefault = res.data.isDefault;
					this.cityPickerValue = res.data.region.value;
					this.region = res.data.region
					this.detailed = res.data.detailed;
				}
			})
		}
	},
	onBackPress() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel();
			return true;
		}
	},
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	}
};
